import React from 'react';

const Card = ({name, email, id}) => {
  return (
    <div className = 'f6 tc bg-light-green dib br4 pa1 ma2 grow bw2 shadow-5'>
      <img src={`https://robohash.org/${id}?200x200`} alt="robots" />
      <div>
        <h2>{name}</h2>
        <p>{email}</p>
      </div>
    </div>
  );
}

export default Card